import React, { useState, useEffect } from 'react';

import './stylesAdminProducts.scss';
import Modal from '../../UIElement/Modal/Modal';
import FormBook from '../../UIElement/Forms/FormBook';
import AdminBookList from '../../UIElement/AdminList/AdminBookList';

const AdminBooks = ({
  fetchAllAdminBooks,
  products,
  editions,
  tags,
  collections,
  users,
  types,
  createOrModifyBook,
  getIdFromObject,
  eraseFormState,
  deleteBook,
}) => {
  const [modal, setModal] = useState(false);
  const [modalContent, setModalContent] = useState(null);

  useEffect(() => {
    fetchAllAdminBooks();
  }, [fetchAllAdminBooks]);
  const openModal = () => setModal(true);
  const closeModal = () => setModal(false);

  const usersAuthor = users.filter(
    (user) => user.role === 'Editeur' || user.role === 'Auteur'
  );
  const usersIllustrator = users.filter(
    (user) => user.role === 'Editeur' || user.role === 'Illustrateur'
  );

  return (
    <>
      <Modal
        show={modal}
        onCancel={closeModal}
        style={{ textAlign: 'center' }}
        button={
          <button onClick={closeModal} style={{ backgroundColor: 'red' }}>
            fermer
          </button>
        }
        content={modalContent}
      />
      <div className="product-list-box">
        <div className="admin-title">
          <h2>Livres</h2>
          <button
            onClick={() => {
              openModal();
              setModalContent(
                <FormBook
                  editions={editions}
                  tags={tags}
                  collections={collections}
                  usersAuthor={usersAuthor}
                  usersIllustrator={usersIllustrator}
                  types={types}
                  createOrModifyBook={createOrModifyBook}
                />
              );
            }}
          >
            Ajouter un livre
          </button>
        </div>
        {products.length === 0 ? <p>Rien à afficher pour l'instant.</p> : null}
        <div className="product-list-container">
          {products.map((product) => (
            <AdminBookList
              product={product}
              eraseFormState={eraseFormState}
              getIdFromObject={getIdFromObject}
              deleteBook={deleteBook}
              openModal={openModal}
              setModalContent={setModalContent}
              editions={editions}
              tags={tags}
              collections={collections}
              usersAuthor={usersAuthor}
              usersIllustrator={usersIllustrator}
              types={types}
              createOrModifyBook={createOrModifyBook}
              fetchAllAdminBooks={fetchAllAdminBooks}
              key={product.id}
            />
          ))}
        </div>
      </div>
    </>
  );
};

export default AdminBooks;
