import React, { useState } from 'react';
import axios from 'axios';
import { toast } from 'react-toastify';

import ButtonForm from '../ButtonForm/ButtonForm';

const MultipleFiles = ({ id, reloadData }) => {
  const [fileToUpload, setFileToUpload] = useState({});

  const onSubmitFile = () => {
    toast.warning('Images en cours de téléchargement');
    const data = new FormData();
    for (let x = 0; x < fileToUpload.length; x++) {
      data.append('picture_file', fileToUpload[x]);
    }
    axios({
      method: 'PUT',
      url: `https://loupargent-oclock.fr/api/admin/products/${id}`,
      data,
    })
      .then(() => {
        toast.info('Les images ont été enregistré');
        reloadData();
      })
      .catch((error) => {
        console.log(error);
        toast.error(`Une erreur est survenue : ${error}`);
      });
  };
  return (
    <form
      className="form"
      onSubmit={(event) => {
        event.preventDefault();
        onSubmitFile();
      }}
    >
      <div className="input-box">
        <label className="label">Image Avatar</label>
        <input
          type="file"
          accept="image/png, image/jpeg, image/jpg"
          name="picture_file"
          className="input"
          multiple
          onChange={(event) => {
            setFileToUpload(event.target.files);
          }}
        />
      </div>
      <ButtonForm type="submit">envoyer</ButtonForm>
    </form>
  );
};

export default MultipleFiles;
