import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import FormBook from '../../UIElement/Forms/FormBook';
import MultipleFiles from '../../UIElement/Forms/Field/MultipleFiles';

const AdminBookList = ({
  product,
  eraseFormState,
  getIdFromObject,
  deleteBook,
  openModal,
  setModalContent,
  editions,
  tags,
  collections,
  usersAuthor,
  usersIllustrator,
  types,
  createOrModifyBook,
  fetchAllAdminBooks,
}) => {
  const convertToDate = (date) => {
    const convertExistingValueToDate = new Date(date);

    return `${convertExistingValueToDate.getDate()}/${
      convertExistingValueToDate.getMonth() + 1
    }/${convertExistingValueToDate.getFullYear()}`;
  };
  return (
    <div className="product-list-item">
      <div
        className="product-list-item_img"
        style={{
          background: `url(https://loupargent-oclock.fr/img/${product.picture_file[0]})`,
          backgroundSize: 'cover',
        }}
      ></div>
      <h1 className="product-list-item_title">{product.name}</h1>
      <span
        className="product-list-item_delete"
        onClick={() => {
          eraseFormState();
          getIdFromObject({ id: product.id });
          window.confirm('Etes vous sûr de vouloir supprimer ce livre ?') &&
            deleteBook();
        }}
      >
        <FontAwesomeIcon icon="trash-alt" />
      </span>
      <div className="product-list-item_description">
        <FontAwesomeIcon icon="quote-left" />
        {product.synopsys.slice(0, 100)}...
        <FontAwesomeIcon icon="quote-right" />
      </div>
      <div className="product-list-item_infos">
        <span>N°{product.id}</span>
        <span>{product.price}€</span>
        <span>{product.pages} pages</span>
      </div>
      <div className="product-list-item_infos">
        <span>{product.weight}kg</span>
        <span>stock : {product.stock_quantity}</span>
        <span>{product.type_name}</span>
      </div>
      <div className="product-list-item_modifications">
        <button
          onClick={() => {
            openModal();
            setModalContent(
              <MultipleFiles id={product.id} reloadData={fetchAllAdminBooks} />
            );
          }}
        >
          <FontAwesomeIcon style={{ margin: '0 0.5rem' }} icon="file-image" />
          Photos
        </button>
        <button
          onClick={() => {
            openModal();
            setModalContent(
              <FormBook
                data={product}
                editions={editions}
                tags={tags}
                collections={collections}
                usersAuthor={usersAuthor}
                usersIllustrator={usersIllustrator}
                types={types}
                createOrModifyBook={createOrModifyBook}
                getIdFromObject={getIdFromObject}
              />
            );
          }}
        >
          <FontAwesomeIcon icon="pen" />
          Modifier
        </button>
      </div>
      <div className="product-list-item_diverse">
        <span>Edition : {product.edition_name}</span>
        <span>Collection : {product.collection_name}</span>
        <span>Copyright : {product.copyright}</span>
        <span>EAN : {product.ean}</span>
        <span>ISBN : {product.isbn}</span>
        <span>Taille : {product.size}</span>
        <span>Date de parution : {convertToDate(product.release_date)}</span>
      </div>
    </div>
  );
};

export default AdminBookList;
