import { connect } from 'react-redux';
import AdminBooks from '../../Components/Admin/AdminProducts/AdminBooks';
import {
  fetchAllAdminBooks,
  createOrModifyBook,
  deleteBook,
} from '../../Redux/actions/listProducts.actions';
import {
  getIdFromObject,
  eraseFormState,
} from '../../Redux/actions/form.actions';

const mapState = (state) => {
  return {
    products: state.listProductsReducer.products,
    editions: state.listProductsReducer.editions,
    tags: state.listProductsReducer.tags,
    collections: state.listProductsReducer.collections,
    users: state.listProductsReducer.users,
    types: state.listProductsReducer.types,
  };
};

const mapDispatch = (dispatch) => ({
  fetchAllAdminBooks: () => {
    dispatch(fetchAllAdminBooks());
  },
  createOrModifyBook: () => {
    dispatch(createOrModifyBook());
  },
  getIdFromObject: (payload) => {
    dispatch(getIdFromObject(payload));
  },
  eraseFormState: () => {
    dispatch(eraseFormState());
  },
  deleteBook: () => {
    dispatch(deleteBook());
  },
});

export default connect(mapState, mapDispatch)(AdminBooks);
