import React, { useEffect } from 'react';

import Field from '../../../Containers/UIElement/Forms/Field/FieldContainer';
import Time from '../../../Containers/UIElement/Forms/Field/TimeContainer';
import Select from '../../../Containers/UIElement/Forms/Field/SelectContainer';
import SelectMultiple from '../../../Containers/UIElement/Forms/Field/SelectMultipleContainer';
import TextArea from '../../../Containers/UIElement/Forms/Field/TextAreaContainer';
import FormContainer from '../../../Containers/UIElement/Forms/FormContainer/FormContainerContainer';
import ButtonForm from './ButtonForm/ButtonForm';

const FormBook = ({
  data = {},
  editions,
  tags,
  collections,
  usersAuthor,
  usersIllustrator,
  types,
  createOrModifyBook,
  getIdFromObject,
}) => {
  useEffect(() => {
    getIdFromObject !== undefined && getIdFromObject({ id: data.id });
  }, [data.id, getIdFromObject]);
  return (
    <FormContainer onSubmitEvent={createOrModifyBook}>
      <Field
        label="Titre"
        name="name"
        type="text"
        existingvalue={data.name}
        placeholder="Titre"
        requiredlabel={true}
        pattern="[\w\W]{1,50}"
        title="Maximum 50 caractères"
        required
      />
      <Select
        label="Edition"
        name="id_edition"
        existingvalue={data.id_edition}
        options={editions}
        requiredlabel={true}
        required
      />
      <Select
        label="Type de produit"
        name="id_type"
        existingvalue={data.id_type}
        options={types}
        requiredlabel={true}
        required
      />
      <SelectMultiple
        label="Auteur"
        name="author"
        options={usersAuthor}
        existingvalue={data.writer}
        multiple
      />
      <SelectMultiple
        label="Illustrateur"
        name="illustrator"
        options={usersIllustrator}
        existingvalue={data.illustrator}
        multiple
      />
      <Field
        label="Prix"
        name="price"
        type="number"
        existingvalue={data.price}
        placeholder="Prix"
        pattern="[\d\.,]{1,}"
        title="Un prix valide en chiffres"
        requiredlabel={true}
        required
      />
      <Field
        label="Nom copyright"
        name="copyright_photo"
        type="text"
        existingvalue={data.copyright_photo}
        placeholder="Nom copyright"
      />
      <Field
        label="ISBN"
        name="isbn"
        type="text"
        existingvalue={data.isbn}
        placeholder="ISBN"
      />
      <Field
        label="Copyright"
        name="copyright"
        type="text"
        existingvalue={data.copyright}
        placeholder="Copyright"
      />
      <Field
        label="Taille"
        name="size"
        type="text"
        existingvalue={data.size}
        placeholder="Taille du livre en A x B cm"
        requiredlabel={true}
        required
      />
      <Field
        label="Poids"
        name="weight"
        type="number"
        existingvalue={data.weight}
        placeholder="Poids du livre en kilogrammes"
        requiredlabel={true}
        required
      />
      <Field
        label="Pages"
        name="pages"
        type="number"
        existingvalue={data.pages}
        placeholder="Nombre de pages"
        pattern="[\d]{1,}"
        title="Pas de caractères spéciaux, un nombre de pages en chiffres"
        requiredlabel={true}
        required
      />
      <Field
        label="EAN"
        name="ean"
        type="text"
        existingvalue={data.ean}
        placeholder="EAN"
      />
      <Time
        label="Parution"
        name="release_date"
        type="text"
        placeholder="Date de parution"
        pattern="^([0-2][0-9]|(3)[0-1])(\/)(((0)[0-9])|((1)[0-2]))(\/)\d{4}$"
        title="Une date valide jj/mm/aaaa, séparé par des /"
        existingvalue={data.release_date}
        requiredlabel={true}
      />
      <Field
        label="Stock"
        name="stock_quantity"
        type="number"
        existingvalue={data.stock_quantity}
        placeholder="Stock"
        pattern="[\d\.,]{1,}"
        title="Un stock en chiffres"
        requiredlabel={true}
        required
      />
      <Select
        label="Collection"
        name="id_collection"
        existingvalue={data.id_collection}
        options={collections}
      />
      <SelectMultiple
        label="Tags"
        name="tag"
        options={tags}
        existingvalue={data.tag}
        multiple
      />
      <Field
        label="Kindle"
        name="url_kindle"
        type="text"
        existingvalue={data.url_kindle}
        placeholder="url du lien Kindle"
      />
      <Field
        label="Booknode"
        name="url_booknode"
        type="text"
        existingvalue={data.url_booknode}
        placeholder="url du lien Booknode"
      />
      <TextArea
        label="Résumé"
        name="synopsys"
        existingvalue={data.synopsys}
        pattern=".{20,}"
        title="Minimum 20 charactères de long"
        requiredlabel={true}
        required
      />
      <TextArea
        label="Extrait"
        name="excerpt"
        existingvalue={data.excerpt}
        pattern=".{20,}"
        title="Minimum 20 charactères de long"
        requiredlabel={true}
      />
      <ButtonForm type="submit">Enregistrer</ButtonForm>
    </FormContainer>
  );
};

export default FormBook;
